import { useState, useEffect, createContext } from "react";
import "lightbox-react/style.css";
import Lightbox from "lightbox-react";

export const MainContext = createContext({});

export const MainProvider = ({ children }) => {

  const [images, setImages] = useState([]);

  const [compare, setCompare] = useState([])

  const [photoIndex, setPhotoIndex] = useState(0);

  const token = localStorage.getItem("token");

  const [isLoading, setLoading] = useState(false)

  const toggleCompare = (prop) => {
      const carray = compare.map(e => e.id);

      if (carray.includes(prop.id)) {
        setCompare([...compare].filter(x => x.id != prop.id));
      }else{
        setCompare(() => [...compare, prop]);
      }

  }

  const showLoginModal = () => {
    const elem = document.querySelector(".login-link.topbar-link");

    if (elem) elem.click();

  }

  const addToFavorites = async (id) => {

    if (!Boolean(token)) {
      showLoginModal();

      return;
    }

    try {

      setLoading(true);

      await axios.post(
        "/api/qsyrys/favorites/add",
        {
          property: id,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      setLoading(false);

    } catch (err) {
      console.log(err);
      setLoading(false);
    }

  }

  const saveSearch = async (searchOpt) => {

    if (!Boolean(token)) {
      showLoginModal();
      return;
    }

    try {

      setLoading(true);

      await axios.post(
        "/api/qsyrys/property/search",
        {
          query: searchOpt,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      setLoading(false);

    } catch (err) {

      console.log(err);
      setLoading(false);

    }

  }

  const removeFavorite = async (id) => {

      if (!Boolean(token)) {
          showLoginModal();
          return;
      }

      try {

        setLoading(true)

        await axios.delete(
          "/api/qsyrys/favorites/remove",
          {
            property: id,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );

        setLoading(false)

      } catch (err) {

        console.log(err);
        setLoading(false)

      }
  }

  return (
    <MainContext.Provider
      value={{
        isLogged: Boolean(token),
        showLoginModal,
        addToFavorites,
        removeFavorite,
        isLoading,
        compare,
        setCompare,
        images,
        setImages,
        setPhotoIndex,
        saveSearch,
        toggleCompare,
      }}
    >
      {Boolean(images.length) && (
        <Lightbox
          mainSrc={images[photoIndex]}
          nextSrc={images[(photoIndex + 1) % images.length]}
          prevSrc={images[(photoIndex + images.length - 1) % images.length]}
          onCloseRequest={() => {
            setPhotoIndex(0);
            setImages([]);
          }}
          onMovePrevRequest={() =>
            setPhotoIndex((photoIndex + images.length - 1) % images.length)
          }
          onMoveNextRequest={() =>
            setPhotoIndex((photoIndex + 1) % images.length)
          }
        />
      )}

      {children}
    </MainContext.Provider>
  );
};
